import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Menu, Dropdown } from 'antd';
import { DownOutlined } from '@ant-design/icons';
import MediaList from '../MediaList/MediaList';
import MainContainer from '../UI/MainContainer/MainContainer';

import styles from './Footer.module.scss';

const langs = {
  en: { nativeName: 'English', icon: 'en' },
  ar: { nativeName: 'عربي', icon: 'ar' },
  de: { nativeName: 'Deutsch', icon: 'de' },
  el: { nativeName: 'Eλληνικά', icon: 'el' },
  es: { nativeName: 'Español', icon: 'es' },
  fi: { nativeName: 'Suomi', icon: 'fi' },
  fr: { nativeName: 'Français', icon: 'fr' },
  zh: { nativeName: '简体中文', icon: 'zh' },
  // zh_HANT: { nativeName: '繁體中文' },
  ja: { nativeName: '日本語', icon: 'ja' },
  pt: { nativeName: 'Português', icon: 'pt' },
  it: { nativeName: 'Italiano', icon: 'it' },
  ro: { nativeName: 'Română', icon: 'ro' },
  ko: { nativeName: '한국의', icon: 'ko' },
  ru: { nativeName: 'Русский', icon: 'ru' },
  bn: { nativeName: 'বাংলা', icon: 'bn' },
  // hr: { nativeName: 'hrvatski' },
  hi: { nativeName: 'हिन्दी或हिंदी', icon: 'hi' },
  hu: { nativeName: 'Magyar', icon: 'hu' },
  vi: { nativeName: 'Tiếng Việt', icon: 'vi' },
};

const Footer = () => {
  const { i18n } = useTranslation();
  const [currentLang, setCurrentLang] = useState('en');
  const langMenu = (
    <Menu>
      {Object.keys(langs).map((lang) => (
        <Menu.Item
          key={lang}
          // type="submit"
          onClick={() => {
            i18n.changeLanguage(lang);
            // setCurrentLang(lang);
          }}>
          {langs[lang].nativeName}
        </Menu.Item>
      ))}
    </Menu>
  );

  useEffect(() => {
    setCurrentLang(i18n.resolvedLanguage);
  }, [i18n.resolvedLanguage]);
  return (
    <div className={styles.Footer}>
      <MainContainer className={styles.mainContainer}>
        <p>Copyright© 2022 GoFundUs. All rights reserved.</p>
        <div className={styles.mediaAndLangs}>
          <MediaList />
          <Dropdown overlay={langMenu}>
            <div className={styles.langButton} onClick={(e) => e.preventDefault()}>
              {langs[currentLang].nativeName}
              &nbsp;&nbsp;&nbsp;
              <DownOutlined />
            </div>
          </Dropdown>
        </div>
      </MainContainer>
    </div>
  );
};

export default Footer;
