import { useState, useContext, createContext, useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { ethers } from 'ethers';

import { USER_API } from '../API';
import { checkTime, TIME_IN_SECOND } from '../utils/utils';

const TOKEN_VALID_DURATION = 5 * TIME_IN_SECOND.DAY * 1000;

const authContext = createContext();

export const useAuth = () => useContext(authContext);

const useProvideAuth = () => {
  const [credential, setCredential] = useState(null);
  const [info, setInfo] = useState(null);

  const signup = async (payload) => {
    let data = null;
    if (!payload) {
      console.log('no account info');
      return data;
    }
    try {
      const res = await USER_API.post('/reg', payload);
      if (res.status === 200) {
        data = res?.data;
      } else {
        console.log(res);
      }
    } catch (err) {
      console.error(err);
    }
    return data;
  };

  const login = async (payload, remember = false) => {
    let data = null;
    if (!payload) {
      console.log('no account info');
      return data;
    }
    try {
      const res = await USER_API.post('/login', payload);
      if (res.status === 200) {
        data = res?.data;
      } else {
        console.log(res);
      }
    } catch (err) {
      console.error(err);
    }

    const token = data?.data?.token;
    if (token) {
      const credentialObj = {
        token,
        expireTimestamp: Date.now() + TOKEN_VALID_DURATION,
      };
      const credentialObjStr = JSON.stringify(credentialObj);
      setCredential(credentialObj);
      sessionStorage.credential = credentialObjStr;
      if (remember) {
        localStorage.credential = credentialObjStr;
      }
    }
    return data;
  };

  const sendEmailVerify = async (payload) => {
    let data = null;
    if (!payload) {
      return data;
    }
    try {
      const res = await USER_API.post('/send_email_verify', payload);
      if (res.status === 200) {
        data = res?.data;
      } else {
        console.log(res);
      }
    } catch (err) {
      console.error(err);
    }
    return data;
  };

  const getInfo = useCallback(async () => {
    let data = null;
    if (!credential) {
      return data;
    }
    try {
      const res = await USER_API.get('/info', { headers: { token: credential.token } });
      if (res.status === 200) {
        data = res?.data;
      } else {
        console.log(res);
      }
    } catch (err) {
      console.error(err);
    }
    const userInfo = data?.data;
    setInfo(userInfo);
    return data;
  }, [credential]);

  const logout = () => {
    setCredential(null);
    setInfo(null);
    sessionStorage.removeItem('credential');
    localStorage.removeItem('credential');
    window.location.reload();
  };

  const changePassword = async (payload) => {
    let data = null;
    if (!(payload && credential)) {
      return data;
    }
    try {
      const res = await USER_API.post('/change_password', payload, {
        headers: { token: credential.token },
      });
      if (res.status === 200) {
        data = res?.data;
      } else {
        console.log(res);
      }
    } catch (err) {
      console.error(err);
    }
    return data;
  };

  const bindAddress = async (address) => {
    let data = null;
    if (!(ethers.utils.isAddress(address) && credential)) {
      return data;
    }
    try {
      const res = await USER_API.post(
        '/bind_wallet',
        { address },
        {
          headers: { token: credential.token },
        },
      );
      if (res.status === 200) {
        data = res?.data;
      } else {
        console.log(res);
      }
    } catch (err) {
      console.error(err);
    }
    return data;
  };

  const resetPassword = async (payload) => {
    let data = null;
    if (!payload) {
      console.log('no account info');
      return data;
    }
    try {
      const res = await USER_API.post('/reset_pwd', payload);
      if (res.status === 200) {
        data = res?.data;
      } else {
        console.log(res);
      }
    } catch (err) {
      console.error(err);
    }
    return data;
  };

  // restore token
  useEffect(() => {
    const restoreCredential = () => {
      // return if no saved credential found
      if (!(sessionStorage.credential || localStorage.credential)) {
        return;
      }
      // restore from sessionStorage or localStorage
      if (sessionStorage.credential) {
        const credentialObj = JSON.parse(sessionStorage.credential);
        if (!checkTime(credentialObj.expireTimestamp)) {
          setCredential(credentialObj);
        } else {
          logout();
          // sessionStorage.removeItem('credential');
        }
      } else {
        const credentialObj = JSON.parse(localStorage.credential);
        if (!checkTime(credentialObj.expireTimestamp)) {
          setCredential(credentialObj);
        } else {
          logout();
          // localStorage.removeItem('credential');
        }
      }
    };
    restoreCredential();
  }, []);

  // get userInfo when credential changes
  useEffect(() => {
    getInfo();
  }, [getInfo]);

  // auto logout
  useEffect(() => {
    let timer;
    if (credential) {
      const { expireTimestamp } = credential;
      if (expireTimestamp && !checkTime(expireTimestamp)) {
        const expireTime = expireTimestamp - Date.now();
        timer = setTimeout(() => {
          logout();
        }, expireTime);
      }
    }
    return () => {
      clearTimeout(timer);
    };
  }, [credential]);

  return {
    signup,
    login,
    sendEmailVerify,
    getInfo,
    logout,
    info,
    changePassword,
    bindAddress,
    resetPassword,
  };
};

export const ProvideAuth = ({ children }) => {
  const auth = useProvideAuth();
  return <authContext.Provider value={auth}>{children}</authContext.Provider>;
};

ProvideAuth.propTypes = {
  children: PropTypes.node.isRequired,
};
