import { useEffect, useState } from 'react';
import { useWallet } from './useWallet';
import { useUpdate } from './useUpdate';
import { useTokenContract } from './useContract';
import { TOKENS } from '../constants/addresses';

// eslint-disable-next-line import/prefer-default-export
export const useAccountTokenBalance = (token, optionalAccount = null) => {
  const tokenContract = useTokenContract(token);
  const { account, ethersProvider } = useWallet();
  const [data, setData] = useState(0);
  const [loading, setLoading] = useState(true);
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    let isMounted = true;
    async function getTokenBalance() {
      try {
        setLoading(true);
        const balance = await tokenContract.balanceOf(optionalAccount || account);
        if (isMounted) {
          setData(balance);
        }
      } catch (err) {
        console.error(err);
        if (isMounted) {
          setData(0);
        }
      } finally {
        if (isMounted) {
          setLoading(false);
        }
      }
    }

    async function getNativeBalance() {
      try {
        setLoading(true);
        const balance = await ethersProvider.getBalance(optionalAccount || account);
        if (isMounted) {
          setData(balance);
        }
      } catch (err) {
        console.error(err);
        if (isMounted) {
          setData(0);
        }
      } finally {
        if (isMounted) {
          setLoading(false);
        }
      }
    }

    if (account || optionalAccount) {
      if (token === TOKENS.NATIVE) {
        getNativeBalance().catch((e) => {
          console.log(e);
        });
      } else {
        getTokenBalance().catch((e) => {
          console.log(e);
        });
      }
    } else if (isMounted) {
      setData(0);
    }
    return () => {
      isMounted = false;
    };
  }, [tokenContract, account, optionalAccount, ethersProvider, token, updateByTimer]);

  return { data, loading };
};
