import { useMemo } from 'react';

import { ethers, Contract } from 'ethers';
// import _ from 'lodash';

import IERC20ABI from '../constants/abis/IERC20.json';
import MULTICALL_ABI from '../constants/abis/MULTICALL.json';
import GO_FUND_US_MANAGER_ABI from '../constants/abis/GO_FUND_US_MANAGER.json';
// import INVITATION from '../constants/abis/INVITATION.json';
import { useWallet } from './useWallet';
import { GO_FUND_US_MANAGER_ADDRESS, MULTICALL_ADDRESS } from '../constants/addresses';

export const useTokenContract = (address) => {
  const { ethersProvider, account } = useWallet();
  return useMemo(() => {
    if (!(ethersProvider && ethers.utils.isAddress(address))) {
      return null;
    }
    return account
      ? new Contract(address, IERC20ABI.abi, ethersProvider.getSigner())
      : new Contract(address, IERC20ABI.abi, ethersProvider);
  }, [ethersProvider, address, account]);
};

export const useGoFundUsManagerContract = () => {
  const { ethersProvider, account, chainInfo } = useWallet();
  return useMemo(() => {
    if (!(ethersProvider && chainInfo)) {
      return null;
    }
    const managerAddress = GO_FUND_US_MANAGER_ADDRESS[chainInfo.shortName.toUpperCase()];
    return account
      ? new Contract(managerAddress, GO_FUND_US_MANAGER_ABI.abi, ethersProvider.getSigner())
      : new Contract(managerAddress, GO_FUND_US_MANAGER_ABI.abi, ethersProvider);
  }, [account, ethersProvider, chainInfo]);
};

export const useMulticallContract = () => {
  const { ethersProvider, chainInfo } = useWallet();
  return useMemo(() => {
    if (!(ethersProvider && chainInfo)) {
      return null;
    }
    const multiCallAddress = MULTICALL_ADDRESS[chainInfo.shortName.toUpperCase()];
    return new Contract(multiCallAddress, MULTICALL_ABI.abi, ethersProvider);
  }, [ethersProvider, chainInfo]);
};

