import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useProjectInfoByHandle } from '../../hooks/useProjectInfo';
import MainContainer from '../../components/UI/MainContainer/MainContainer';
import styles from './ProjectDesign.module.scss';
import DesignForm from '../../components/ProjectDesign/DesignForm/DesignForm';
import { useWallet } from '../../hooks/useWallet';

const ProjectDesign = () => {
  const { t } = useTranslation();
  const { projectHandle } = useParams();
  const { projectInfo } = useProjectInfoByHandle(projectHandle);
  const [projectData, setProjectData] = useState();
  const { account } = useWallet();
  const [dataSet, setDataSet] = useState(false);

  useEffect(() => {
    const testProject = async () => {
      if (!projectInfo || dataSet || !account) {
        return;
      }
      if (projectInfo?.projectOwner?.toLowerCase() === account) {
        setProjectData(projectInfo);
        setDataSet(true);
      }
    };
    testProject().catch((e) => {
      console.log(e);
    });
  }, [account, projectInfo, dataSet]);

  return (
    <div className={styles.ProjectDesign}>
      <section
        className={styles.banner}
        style={{ backgroundImage: 'url(/static/images/projectDesign/bannerBg.jpg)' }}>
        <div className={styles.innerWrapper}>
          <MainContainer className={styles.bannerContainer}>
            <h2>{t('design-your-project')} </h2>
            <div className={styles.instructionWrap}>
              <a href="https://youtu.be/3BRFvM0rtUA" target="_blank" rel="noopener noreferrer">
                <img src="/static/images/projectDesign/video.png" alt="video" />{' '}
                {t('project-design-instructional-video')}
              </a>
              <a
                href="https://gofundus.fund/static/guide.pdf"
                target="_blank"
                rel="noopener noreferrer">
                <img src="/static/images/projectDesign/pdf.png" alt="pdf" />
                {t('project-design-creation-guide')}
              </a>
            </div>
          </MainContainer>
        </div>
      </section>

      <MainContainer>
        <DesignForm project={projectData} />
      </MainContainer>
    </div>
  );
};

export default ProjectDesign;
