import React, { useState, useEffect } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import classnames from 'classnames';
import { MenuOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import { Modal } from 'antd';

import MainContainer from '../UI/MainContainer/MainContainer';
import MainButton from '../MainButton/MainButton';
import WalletList from '../WalletList';
import { truncAddress } from '../../utils/utils';
import { useWallet } from '../../hooks/useWallet';
import WalletDetail from '../WalletDetail';
import { useWindowDimensions } from '../../hooks/useWindowDimensions';
// import { useAuthMenu } from '../../hooks/useAuthMenu';
import AuthMenu from '../Auth/AuthMenu/AuthMenu';
import AuthButton from '../Auth/AuthButton/AuthButton';
import ChainButton from '../ChainButton/ChainButton';

import styles from './Header.module.scss';

const BREAKPOINT = 765;

const Header = () => {
  const { width } = useWindowDimensions();
  const [menuCollapsed, setMenuCollapsed] = useState(width < BREAKPOINT || false);
  const [walletListModalStatus, setWalletListModalStatus] = useState(false);
  const [walletDetailModalStatus, setWalletDetailModalStatus] = useState(false);
  // i18n.resolvedLanguage ? langs[i18n.resolvedLanguage].nativeName : 'English',
  const { t } = useTranslation();
  const wallet = useWallet();
  // const { setToggleShowWalletListModal } = useAuthMenu();

  useEffect(() => {
    if (width > BREAKPOINT) {
      setMenuCollapsed(false);
    } else {
      setMenuCollapsed(true);
    }
  }, [width]);

  const toggleMenu = () => {
    if (width < BREAKPOINT) {
      setMenuCollapsed(!menuCollapsed);
    }
  };

  const toggleWalletListModal = () => {
    setWalletListModalStatus((state) => !state);
  };

  const toggleWalletDetailModal = () => {
    setWalletDetailModalStatus((state) => !state);
  };

  // useEffect(() => {
  //   setToggleShowWalletListModal(() => toggleWalletListModal);
  // }, [setToggleShowWalletListModal]);

  return (
    <div className={styles.Header}>
      <MainContainer className={styles.headerContainer}>
        <div className={styles.burgerWrapper}>
          <MenuOutlined onClick={toggleMenu} className={styles.hamburger} />
          <Link to="/">
            <img src="/static/images/header/logo.png" alt="logo" className={styles.logo} />
          </Link>
        </div>
        <AuthMenu />
        {!menuCollapsed && (
          <div className={styles.menuWrap}>
            <div className={styles.navLinkWrap}>
              <div className={styles.arrow} onClick={toggleMenu}>
                <ArrowLeftOutlined />
              </div>

              <NavLink
                onClick={toggleMenu}
                to="/projects"
                className={(isActive) =>
                  classnames(styles.navLink, { [styles.navLinkActive]: isActive })
                }>
                <h2>{t('home-header-tabs-projects')}</h2>
              </NavLink>
              <NavLink
                onClick={toggleMenu}
                to="/design"
                className={(isActive) =>
                  classnames(styles.navLink, { [styles.navLinkActive]: isActive })
                }>
                <h2>{t('home-header-tabs-Design')}</h2>
              </NavLink>
            </div>
            <div className={styles.buttonWrap}>
              <ChainButton className={styles.chainButton} />
              {wallet.account ? (
                <MainButton
                  className={styles.connectButton}
                  onClick={() => {
                    toggleWalletDetailModal();
                    toggleMenu();
                  }}>
                  {truncAddress(wallet.account)}
                  <div className={styles.check}>
                    <img src="/static/images/header/checked.png" alt="check" />
                  </div>
                </MainButton>
              ) : (
                <MainButton
                  className={classnames(styles.connectButton, styles.green)}
                  onClick={() => {
                    toggleWalletListModal();
                    toggleMenu();
                  }}>
                  {t('home-header-button-ConnectWallet')}
                </MainButton>
              )}
            </div>
          </div>
        )}
        <AuthButton />
        <Modal
          title={null}
          visible={walletListModalStatus}
          onOk={toggleWalletListModal}
          onCancel={toggleWalletListModal}
          wrapClassName={styles.walletModalWrapper}
          width={370}
          centered
          closable={false}
          footer={null}>
          <WalletList toggleWalletListModal={toggleWalletListModal} />
        </Modal>
        <Modal
          title={null}
          visible={walletDetailModalStatus}
          onOk={toggleWalletDetailModal}
          onCancel={toggleWalletDetailModal}
          wrapClassName={styles.walletModalWrapper}
          width={400}
          centered
          closable={false}
          footer={null}>
          <WalletDetail toggleWalletDetailModal={toggleWalletDetailModal} />
        </Modal>
      </MainContainer>
    </div>
  );
};

export default Header;
