import React from 'react';
import classNames from 'classnames';
import { useTranslation } from 'react-i18next';

import { useAuth } from '../../../hooks/useAuth';
import { useAuthMenu } from '../../../hooks/useAuthMenu';
import MainButton from '../../MainButton/MainButton';

import styles from './AuthButton.module.scss';

const AuthButton = () => {
  const authMenu = useAuthMenu();
  const { info } = useAuth();
  const { t } = useTranslation();

  return (
    <div className={styles.AuthButton}>
      {info ? (
        <div
          className={classNames(styles.postLogin, styles.authMenuWrap)}
          onClick={authMenu.toggleShowProfileModal}>
          <img src={`/static/images/profile/avatar/${info.icon}.png`} alt="avatar" />
          <div className={styles.textButton}>{t('profile-profile')}</div>
        </div>
      ) : (
        <div className={classNames(styles.preLogin, styles.authMenuWrap)}>
          <div className={classNames(styles.textButton)} onClick={authMenu.showSignupModal}>
            {t('home-header-button-career')}
          </div>
          <MainButton
            className={classNames(styles.login, styles.button)}
            secondary
            onClick={authMenu.showLoginModal}>
            {t('auth-menu-login')}
          </MainButton>
        </div>
      )}
    </div>
  );
};

export default AuthButton;
