import React from 'react';
import { useParams } from 'react-router-dom';
import styles from './ProjectDetail.module.scss';
import MainContainer from '../../components/UI/MainContainer/MainContainer';
import ProjectIntro from '../../components/ProjectDetail/ProjectIntro/ProjectIntro';
import ProjectAction from '../../components/ProjectDetail/ProjectAction/ProjectAction';
import {
  // useProjectListInvitationInfo,
  useProjectInfoByHandleWithDonorInfo,
} from '../../hooks/useProjectInfo';
// import { REFERRAL_LIST } from '../../constants/addresses';

const ProjectDetail = () => {
  const param = useParams();
  const { projectHandle } = param;

  const { projectInfo } = useProjectInfoByHandleWithDonorInfo(projectHandle);
  // const invitationInfo = useProjectListInvitationInfo(projectHandle);
  //
  // console.log(invitationInfo);

  return (
    <div className={styles.ProjectDetail}>
      <MainContainer>
        {!projectInfo && (
          <div className={styles.loadingPlaceholder}>
            <img src="/static/images/loading/loadingIcon.gif" alt="Loading" />
          </div>
        )}
        {projectInfo && (
          <>
            <ProjectIntro item={projectInfo} />
            <br />
            <ProjectAction item={projectInfo} />
          </>
        )}
      </MainContainer>
    </div>
  );
};

export default ProjectDetail;
