import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'antd';
import { CheckOutlined } from '@ant-design/icons';
import classNames from 'classnames';

import styles from './AvatarSelector.module.scss';

const AVATARS = [...Array(12).keys()].map((item) => item + 1);

const AvatarSelector = (props) => {
  const { className, avatar, onClick } = props;
  return (
    <div className={classNames(styles.starterBlock, className)}>
      <div className={styles.starterLogo}>
        <Row gutter={[5, 5]}>
          {AVATARS.map((item) => (
            <Col span={6} key={item}>
              <div
                className={classNames(styles.starterItem, {
                  [styles.selected]: item === avatar,
                })}
                onClick={() => {
                  onClick(item);
                }}>
                <img src={`/static/images/profile/avatar/${item}.png`} alt="starterlogo" />
                <div className={styles.corner} />
                <CheckOutlined className={styles.checkMark} />
              </div>
            </Col>
          ))}
        </Row>
      </div>
    </div>
  );
};
AvatarSelector.propTypes = {
  className: PropTypes.string,
  avatar: PropTypes.number.isRequired,
  onClick: PropTypes.func.isRequired,
};
AvatarSelector.defaultProps = { className: '' };

export default AvatarSelector;
