import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Trans, useTranslation } from 'react-i18next';
import classnames from 'classnames';
import styles from './Projects.module.scss';
import MainContainer from '../../components/UI/MainContainer/MainContainer';
import ProjectList from '../../components/Projects/ProjectList/ProjectList';
import { useAllProjectsInfo } from '../../hooks/useProjectInfo';
import MainButton from '../../components/MainButton/MainButton';

const LIST_TAB = { TRENDING: 0, END: 1 };

const Projects = () => {
  const [onGoingProjects, setOnGoingProjects] = useState([]);
  const [endedProjects, setEndedProjects] = useState([]);

  const { t } = useTranslation();
  const [listTab, setListTab] = useState(LIST_TAB.TRENDING);
  const { loading, projectsInfo } = useAllProjectsInfo();

  useEffect(() => {
    if (!loading && projectsInfo) {
      setOnGoingProjects(projectsInfo.filter((item) => item.active));
      setEndedProjects(projectsInfo.filter((item) => !item.active));
    }
  }, [projectsInfo, loading]);
  return (
    <div className={styles.Projects}>
      <MainContainer>
        <div className={styles.introWrap}>
          <div className={styles.textWrap}>
            <h2>{t('project-title')}</h2>
            <p>
              <Trans i18nKey="project-desc">
                The GoFundUs protocol is open to anyone, and project configurations can vary widely.
                There are risks associated with interacting with all projects on the protocol.
                Projects built on the protocol are not endorsed or vetted by GoFundUsDAO. Do your
                own research and understand the risks before committing your funds.
              </Trans>
            </p>
          </div>
        </div>
        <div className={styles.projectListWrap}>
          <div className={styles.tabWrap}>
            <div
              className={classnames(styles.tab, {
                [styles.active]: listTab === LIST_TAB.TRENDING,
              })}
              onClick={() => {
                setListTab(LIST_TAB.TRENDING);
              }}>
              <h2>{t('project-trending')}</h2>
            </div>
            <div
              className={classnames(styles.tab, {
                [styles.active]: listTab === LIST_TAB.END,
              })}
              onClick={() => {
                setListTab(LIST_TAB.END);
              }}>
              <h2>{t('project-end')}</h2>
            </div>
          </div>
          <ProjectList
            items={
              (listTab === LIST_TAB.TRENDING && onGoingProjects) ||
              (listTab === LIST_TAB.END && endedProjects)
            }
          />
        </div>

        <div className={styles.linkWrap}>
          <h1>{t('design-your-project')}</h1>
          <Link className={styles.link} to="/design">
            <MainButton>Go</MainButton>
          </Link>
        </div>
      </MainContainer>
    </div>
  );
};

export default Projects;
