import React, { useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Form, Input, Checkbox, message } from 'antd';

import { useTranslation } from 'react-i18next';
import MainButton from '../../MainButton/MainButton';

import styles from './Login.module.scss';
import formStyles from '../AuthForm.module.scss';
import { useAuth } from '../../../hooks/useAuth';
import { AUTH_MODAL_STATE } from '../../../utils/authUtils';

const Login = (props) => {
  const { toggleModal, switchContext } = props;
  const [form] = Form.useForm();
  const initialValues = { remember: true };
  const auth = useAuth();
  const [loading, setLoading] = useState(false);
  const { t } = useTranslation();

  const handleRes = (res) => {
    const code = res?.code;
    let msg = '';
    switch (code) {
      case 0:
        msg = t('login-success');
        message.success(msg);
        toggleModal();
        return;
      case 1:
      case 500:
        msg = t('login-invalid-username-or-password');
        message.error(msg);
        form.setFields([
          { name: 'email', errors: [msg] },
          { name: 'password', errors: [msg] },
        ]);
        return;
      default:
        msg = t('login-unknown-error');
        message.error(msg);
        console.log(res);
    }
  };

  const handleLogin = async () => {
    try {
      setLoading(true);
      await form.validateFields();
      const data = form.getFieldsValue(true);
      const payload = {
        email: data.email,
        passwd: data.password,
      };
      const { remember } = data;
      const res = await auth.login(payload, remember);
      handleRes(res);
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className={classNames(styles.Login, formStyles.AuthForm)}>
      <div className={formStyles.titleWrap}>
        <h1>{t('auth-menu-login-title')}</h1>
        <img src="/static/images/close.png" alt="close" onClick={toggleModal} />
      </div>
      <Form layout="vertical" form={form} initialValues={initialValues} requiredMark={false}>
        <Form.Item
          label={t('sign-up-email')}
          name="email"
          rules={[
            { required: true, message: t('sign-up-enter-your-email') },
            { type: 'email', message: t('sign-up-enter-valid-email') },
          ]}>
          <Input placeholder={t('sign-up-enter-your-email-placeholder')} />
        </Form.Item>
        <Form.Item
          label={t('sign-up-password')}
          name="password"
          rules={[{ required: true, message: t('sign-up-enter-your-password') }]}>
          <Input placeholder={t('sign-up-enter-your-password-placeholder')} type="password" />
        </Form.Item>
        <Form.Item>
          <Form.Item name="remember" valuePropName="checked" noStyle>
            <Checkbox>{t('login-remember-me')}</Checkbox>
          </Form.Item>

          <p
            className={classNames(formStyles.forget, formStyles.link)}
            onClick={() => switchContext(AUTH_MODAL_STATE.RESET)}>
            {t('login-forgot-password')}
          </p>
        </Form.Item>
      </Form>
      <div className={formStyles.actionWrap}>
        <MainButton className={formStyles.actionButton} loading={loading} onClick={handleLogin}>
          {t('deseign-submit')}
        </MainButton>
        <p>
          {t('login-no-account')}{' '}
          <span className={formStyles.link} onClick={() => switchContext(AUTH_MODAL_STATE.SIGNUP)}>
            {t('auth-menu-sign-up')}
          </span>
        </p>
      </div>
    </div>
  );
};

Login.propTypes = {
  toggleModal: PropTypes.func.isRequired,
  switchContext: PropTypes.func.isRequired,
};

Login.defaultProps = {};

export default Login;
