import React, { useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Form, Input, message } from 'antd';
import { useTranslation } from 'react-i18next';

import { useAuth } from '../../../hooks/useAuth';
import MainButton from '../../MainButton/MainButton';
import { AUTH_MODAL_STATE, EMAIL_TYPE } from '../../../utils/authUtils';
import SendEmailVerify from '../SendEmailVerify/SendEmailVerify';

import styles from './ResetPassword.module.scss';
import formStyles from '../AuthForm.module.scss';

const ResetPassword = (props) => {
  const { toggleModal, switchContext } = props;
  const [form] = Form.useForm();
  const { resetPassword } = useAuth();
  const [loading, setLoading] = useState(false);
  const { t } = useTranslation();

  const handleRes = (res) => {
    const code = res?.code;
    let msg = '';
    switch (code) {
      case 0:
        msg = t('reset-password-success');
        message.success(msg);
        switchContext(AUTH_MODAL_STATE.LOGIN);
        return;
      case 1:
        msg = t('reset-confirm-account-not-exist');
        message.error(msg);
        form.setFields([{ name: 'email', errors: [msg] }]);
        return;
      case 3:
        msg = t('sign-up-invalid-verification');
        message.error(msg);
        form.setFields([{ name: 'verification', errors: [msg] }]);
        return;
      default:
        msg = t('reset-password-unknown-error');
        message.error(msg);
        console.log(res);
    }
  };

  const submitHandler = async () => {
    try {
      setLoading(true);
      await form.validateFields();
      const data = form.getFieldsValue(true);
      const payload = {
        email: data.email,
        verify_code: data.verification,
        new_passwd: data.newPassword,
      };
      const res = await resetPassword(payload);
      handleRes(res);
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className={classNames(styles.ResetPassword, formStyles.AuthForm)}>
      <div className={formStyles.titleWrap}>
        <h1>Reset Password</h1>
        <img src="/static/images/close.png" alt="close" onClick={toggleModal} />
      </div>
      <Form layout="vertical" form={form} requiredMark={false}>
        <Form.Item
          label={t('sign-up-email')}
          name="email"
          rules={[
            { required: true, message: t('sign-up-enter-your-email') },
            { type: 'email', message: t('sign-up-enter-valid-email') },
          ]}>
          <Input placeholder={t('sign-up-enter-your-email-placeholder')} />
        </Form.Item>
        <Form.Item
          label={t('reset-new-password')}
          name="newPassword"
          rules={[
            { required: true, message: t('reset-password-please-enter-your-new-password') },
            {
              type: 'string',
              min: 8,
              max: 25,
              message: t('reset-password-length-warning'),
            },
            {
              pattern: /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).+$/,
              message: t('reset-password-pattern-warning'),
            },
          ]}>
          <Input type="password" placeholder={t('reset-new-password')} />
        </Form.Item>
        <Form.Item
          name="confirmPassword"
          label={t('reset-confirm-password')}
          dependencies={['New Password']}
          rules={[
            {
              required: true,
              message: t('reset-password-please-confirm-your-new-password'),
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('newPassword') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error(t('sign-up-password-not-match')));
              },
            }),
          ]}>
          <Input type="password" placeholder={t('reset-confirm-password')} />
        </Form.Item>
        <Form.Item label={t('sign-up-verification-code')} required>
          <Form.Item
            name="verification"
            rules={[{ required: true, message: t('sign-up-please-enter-your-verification-code') }]}
            noStyle>
            <Input style={{ width: '50%' }} placeholder={t('sign-up-enter-the-code')} />
          </Form.Item>

          <SendEmailVerify
            className={classNames(formStyles.link, formStyles.sendVerify)}
            form={form}
            type={EMAIL_TYPE.RESET_PASSWORD}
          />
        </Form.Item>
      </Form>
      <div className={formStyles.actionWrap}>
        <MainButton className={formStyles.actionButton} loading={loading} onClick={submitHandler}>
          {t('deseign-submit')}
        </MainButton>
        <p>
          {t('reset-password-back-to')}{' '}
          <span className={formStyles.link} onClick={() => switchContext(AUTH_MODAL_STATE.LOGIN)}>
            {t('auth-menu-login')}
          </span>
        </p>
      </div>
    </div>
  );
};

ResetPassword.propTypes = {
  toggleModal: PropTypes.func.isRequired,
  switchContext: PropTypes.func.isRequired,
};

export default ResetPassword;
