import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { useTranslation } from 'react-i18next';
import { message } from 'antd';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import MainButton from '../../MainButton/MainButton';

import styles from './InvitationInfo.module.scss';
import { useAuth } from '../../../hooks/useAuth';

const InvitationInfo = (props) => {
  const { className } = props;
  const { t } = useTranslation();
  const { info } = useAuth();

  return (
    <div className={classNames(styles.invitationInfoWrapper, className)}>
      <div className={styles.infoCol}>
        <div className={styles.textContent}>
          <p>{t('invitation-info-invitation-code')}</p>
          <input readOnly className={styles.input} value={info.invitation_code} />
        </div>
        <CopyToClipboard
          text={info.invitation_code}
          onCopy={() => {
            message.success(t('wallet-detail-copied-to-clipboard'));
          }}>
          <MainButton className={styles.copyButton}>{t('wallet-detail-copy')}</MainButton>
        </CopyToClipboard>
      </div>
      <div className={styles.infoCol}>
        <div className={styles.textContent}>
          <p>{t('invitation-info-invitation-link')}</p>
          <input
            readOnly
            className={styles.input}
            value={`gofundus.fund/#/?invitation=${info.invitation_code}`}
          />
        </div>
        <CopyToClipboard
          text={`gofundus.fund/#/?invitation=${info.invitation_code}`}
          onCopy={() => {
            message.success(t('wallet-detail-copied-to-clipboard'));
          }}>
          <MainButton className={styles.copyButton}>{t('wallet-detail-copy')}</MainButton>
        </CopyToClipboard>
      </div>
    </div>
  );
};

InvitationInfo.propTypes = {
  className: PropTypes.string,
};
InvitationInfo.defaultProps = { className: '' };

export default InvitationInfo;
