import { Switch, Route, Redirect } from 'react-router-dom';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
// import Home from './views/Home/Home';
import Projects from './views/Projects/Projects';
import ProjectDetail from './views/ProjectDetail/ProjectDetail';
import ProjectDesign from './views/ProjectDesign/ProjectDesign';
import Profile from './views/Profile/Profile';
import Invitation from './components/Auth/Invitation/Invitation';

import './App.scss';

function App() {
  return (
    <div className="App">
      <Header />
      <div className="mainContentWrap">
        <Invitation />
        <Switch>
          {/* <Route path="/" exact>
            <Home />
          </Route> */}
          <Route path="/projects" exact>
            <Projects />
          </Route>
          <Route path="/projects/:projectHandle" exact>
            <ProjectDetail />
          </Route>
          <Route path="/design" exact render={() => <ProjectDesign key={Date.now()} />} />
          <Route path="/design/:projectHandle" render={() => <ProjectDesign key={Date.now()} />} />
          <Route path="/profile">
            <Profile />
          </Route>
          <Redirect to="/projects" />
        </Switch>
      </div>
      <Footer />
    </div>
  );
}

export default App;
