import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { message } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import { useTranslation } from 'react-i18next';

import { useAuth } from '../../../hooks/useAuth';

import styles from './SendEmailVerify.module.scss';

const COOL_DOWN = 60;

const SendEmailVerify = (props) => {
  const { form, className, type } = props;
  const [timeLeft, setTimeLeft] = useState(0);
  const [loading, setLoading] = useState(false);
  const auth = useAuth();
  const { t } = useTranslation();

  const handleRes = (res) => {
    const code = res?.code;

    switch (code) {
      case 0:
        message.success(t('send-email-verification-sent'));
        setTimeLeft(COOL_DOWN);
        return;
      default:
        message.error(t('login-unknown-error'));
        console.log(res);
    }
  };

  const handleSend = async () => {
    if (timeLeft > 0) {
      return;
    }
    try {
      await form.validateFields(['email']);
      const data = form.getFieldsValue(['email']);
      setLoading(true);
      const res = await auth.sendEmailVerify({ email: data.email, type });
      handleRes(res);
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    const timer = setTimeout(() => {
      if (timeLeft > 0) {
        setTimeLeft(timeLeft - 1);
      }
    }, 1000);
    return () => {
      clearTimeout(timer);
    };
  }, [timeLeft]);

  return (
    <div className={classNames(styles.SendEmailVerify, className)} onClick={handleSend}>
      {loading && <LoadingOutlined />}
      {!loading && (timeLeft > 0 ? `${timeLeft}s` : 'Send')}
    </div>
  );
};

SendEmailVerify.propTypes = {
  form: PropTypes.objectOf(PropTypes.any).isRequired,
  className: PropTypes.string,
  type: PropTypes.string.isRequired,
};
SendEmailVerify.defaultProps = {
  className: '',
};

export default SendEmailVerify;
