import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import classNames from 'classnames';
import numeral from 'numeral';
import { QuestionCircleOutlined } from '@ant-design/icons';
import { Tooltip } from 'antd';
import { useAuth } from '../../../hooks/useAuth';

import styles from './InviterInfo.module.scss';
// import { truncAddress } from '../../../utils/utils';

const InviterInfo = (props) => {
  const { className } = props;
  const { t } = useTranslation();
  const { info } = useAuth();

  return (
    <div className={classNames(styles.inviterInfoWrapper, className)}>
      {/* <div>
        <p>{t('inviter-info-inviter')}:</p>
        <span>{truncAddress(inviterRecords.inviter)}</span>
      </div> */}
      <div>
        <p>{t('inviter-info-total-referrals')}:</p> <span>{info.invitation_statistics['1']}</span>
      </div>
      <div>
        <p>
          {t('inviter-info-total-points')}:&nbsp;
          <Tooltip title={t('inviter-info-total-point-tooltip')}>
            <QuestionCircleOutlined />
          </Tooltip>
        </p>
        <p className={styles.totalPoints}>
          <img src="/static/images/icons/pointsicon.png" alt="points" />
          <span>{numeral(Number(info.point_total) / 1000000).format('0,0.[00]')}</span>
        </p>
      </div>
    </div>
  );
};

InviterInfo.propTypes = {
  className: PropTypes.string,
};
InviterInfo.defaultProps = { className: '' };

export default InviterInfo;
