/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useMemo, useState } from 'react';
import { utils, constants } from 'ethers';
import { useGoFundUsManagerContract, useMulticallContract } from './useContract';
import GO_FUND_US_MANAGER_ABI from '../constants/abis/GO_FUND_US_MANAGER.json';
import { multicallHelper } from '../utils/multicall';
import { decodeBytesToObject, getTokenName, validateProject } from '../utils/utils';
import { useWallet } from './useWallet';
import TOKEN_PRICE from '../constants/tokens';
import { useUpdate } from './useUpdate';
import { REFERRAL_LIST, TOKENS } from '../constants/addresses';

export const decodeDistributions = (distributions) =>
  distributions.map((item) => ({
    perMillion: item.perMillion.toNumber(),
    who: item.who,
  }));
export const decodeProjectInfo = (projectInfo, account, chainShortName) => {
  const price = projectInfo.price.toString();
  const totalDonation = utils.formatEther(projectInfo.totalDonation);
  const tokenTotalSupply = totalDonation * price;
  const now = +new Date() / 1000;
  const donationDeadline = projectInfo.donationDeadline.toNumber();
  const reservePerMillion = projectInfo.reservePerMillion.toNumber();
  return {
    id: utils.parseBytes32String(projectInfo.id),
    projectName: projectInfo.projectName,
    tokenName: projectInfo.tokenName,
    tokenSymbol: projectInfo.tokenSymbol,
    description: decodeBytesToObject(projectInfo.description),
    createTimestamp: projectInfo.createTimestamp.toNumber(),

    projectOwner: projectInfo.projectOwner,

    donationDeadline,

    donationToken: projectInfo.donationToken,
    donationTokenName: getTokenName(projectInfo.donationToken, TOKENS[chainShortName]),
    donationDistribution: decodeDistributions(projectInfo.donationDistribution),
    totalDonationDistributionPerMillion: projectInfo.totalDonationDistributionPerMillion.toString(),

    donationCap: utils.formatEther(projectInfo.donationCap),
    minimumDonationAmount: utils.formatEther(projectInfo.minimumDonationAmount),

    targetToken: projectInfo.targetToken,
    price,
    targetTaken: projectInfo.targetTaken,

    reservePerMillion,
    reserveDistribution: decodeDistributions(projectInfo.reserveDistribution),
    totalReserveDistributionPerMillion: projectInfo.totalReserveDistributionPerMillion.toString(),

    totalDonation: utils.formatEther(projectInfo.totalDonation),

    mintable: projectInfo.mintable,

    actionBriefCursor: projectInfo.actionBriefCursor.toString(),

    isOwner: account && account.toLowerCase() === projectInfo.projectOwner.toLowerCase(),
    tokenTotalSupply,
    active: now < donationDeadline || donationDeadline === 0,
    reservePercent: reservePerMillion / 10000,
    amountLeft:
      projectInfo.donationCap > 0
        ? utils.formatEther(projectInfo.donationCap.sub(projectInfo.totalDonation))
        : constants.MaxUint256,
  };
};
export const decodeDonorInfo = (donorInfo) => ({
  donationAmount: utils.formatEther(donorInfo.donationAmount),
  inviter: donorInfo.inviter,
  totalInviteesAmount: utils.formatEther(donorInfo.totalInviteesAmount),
  claimed: donorInfo.claimed,
  claimWithBurned: donorInfo.claimWithBurned,
  claimable: !donorInfo.claimed && utils.formatEther(donorInfo.donationAmount) > 0,
});

export const useNumberOfDonorsByHandles = (handles) => {
  const [numberOfDonors, setNumberOfDonors] = useState([]);
  const [loading, setLoading] = useState(true);
  const multicallContract = useMulticallContract();
  const gfuManagerContract = useGoFundUsManagerContract();
  const { updateByTimer } = useUpdate();
  const calls = useMemo(() => {
    if (!(handles && gfuManagerContract)) {
      return null;
    }
    return handles.map((id) => [
      gfuManagerContract.address,
      'donationAddressesLength',
      [utils.formatBytes32String(id)],
    ]);
  }, [handles, gfuManagerContract]);

  useEffect(() => {
    let isMounted = true;
    async function getNumberOfDonors() {
      try {
        if (calls) {
          const results = await multicallHelper(
            multicallContract,
            GO_FUND_US_MANAGER_ABI.abi,
            calls,
          );
          if (results && isMounted) {
            setNumberOfDonors(results.map((item) => item[0].toNumber()));
          }
        }
      } catch (e) {
        console.log(e);
      }
    }
    getNumberOfDonors().then(() => {
      if (isMounted) {
        setLoading(false);
      }
    });
    return () => {
      isMounted = false;
    };
  }, [multicallContract, calls, updateByTimer]);
  return {
    loading,
    numberOfDonors,
  };
};

export const useProjectInfoByHandle = (handle) => {
  const [projectInfo, setProjectInfo] = useState(null);
  const [loading, setLoading] = useState(true);
  const gfuManagerContract = useGoFundUsManagerContract();
  const { account, chainInfo } = useWallet();
  const handles = useMemo(() => [handle], [handle]);
  const { loading: numberOfDonorsLoading, numberOfDonors } = useNumberOfDonorsByHandles(
    handle ? handles : null,
  );
  useEffect(() => {
    let isMounted = true;
    async function getProjectInfo() {
      try {
        if (!numberOfDonorsLoading && gfuManagerContract && handle && numberOfDonors && chainInfo) {
          const project = await gfuManagerContract.projectRecords(
            utils.formatBytes32String(handle),
          );
          if (isMounted) {
            setProjectInfo(
              validateProject(project)
                ? {
                    ...decodeProjectInfo(project, account, chainInfo.shortName.toUpperCase()),
                    numberOfDonors,
                  }
                : null,
            );
          }
        }
      } catch (e) {
        console.log(e);
      }
    }
    getProjectInfo()
      .then(() => {
        if (isMounted) {
          setLoading(false);
        }
      })
      .catch((e) => {
        console.log(e);
      });
    return () => {
      isMounted = false;
    };
  }, [gfuManagerContract, handle, account, numberOfDonors, numberOfDonorsLoading]);
  return {
    loading,
    projectInfo,
  };
};

export const useProjectInfoByHandleWithDonorInfo = (handle) => {
  const [projectInfo, setProjectInfo] = useState(null);
  const [loading, setLoading] = useState(null);
  const { loading: projectInfoLoading, projectInfo: projectInfoWithoutDonorInfo } =
    useProjectInfoByHandle(handle);
  const gfuManagerContract = useGoFundUsManagerContract();
  const { account } = useWallet();
  useEffect(() => {
    let isMounted = true;
    async function getProjectInfo() {
      try {
        if (projectInfoWithoutDonorInfo && !projectInfoLoading) {
          const donorInfo = await gfuManagerContract.donationRecords(
            utils.formatBytes32String(handle),
            account || '0x0000000000000000000000000000000000000000',
          );
          const claimable = await gfuManagerContract.claimAble(
            utils.formatBytes32String(handle),
            account || '0x0000000000000000000000000000000000000000',
          );
          if (isMounted) {
            setProjectInfo({
              ...projectInfoWithoutDonorInfo,
              donorInfo: {
                ...decodeDonorInfo(donorInfo),
                claimableAmount: utils.formatEther(claimable),
              },
            });
          }
        }
      } catch (e) {
        console.log(e);
      }
    }

    getProjectInfo().then(() => {
      if (isMounted) {
        setLoading(false);
      }
    });
    return () => {
      isMounted = false;
    };
  }, [gfuManagerContract, handle, account, projectInfoWithoutDonorInfo, projectInfoLoading]);

  return {
    loading,
    projectInfo,
  };
};

export const useAllProjectIds = () => {
  const [ids, setIds] = useState(null);
  const [loading, setLoading] = useState(true);
  const multicallContract = useMulticallContract();
  const gfuManagerContract = useGoFundUsManagerContract();
  const { updateByTimer } = useUpdate();
  useEffect(() => {
    let isMounted = true;
    async function getIds() {
      try {
        if (multicallContract && gfuManagerContract && gfuManagerContract.address) {
          const projectIdsLength = (await gfuManagerContract.projectIdsLength()).toNumber();
          const idCalls = [];
          for (let i = 0; i < projectIdsLength; i++) {
            idCalls.push([gfuManagerContract.address, 'projectIdsAt', [i]]);
          }
          const idsRes = await multicallHelper(
            multicallContract,
            GO_FUND_US_MANAGER_ABI.abi,
            idCalls,
          );
          if (idsRes && isMounted) {
            setIds(idsRes.map((item) => utils.parseBytes32String(item[0])));
          }
        }
      } catch (e) {
        console.log(e);
      }
    }
    getIds()
      .then(() => {
        if (isMounted) {
          setLoading(false);
        }
      })
      .catch((e) => {
        console.log(e);
      });
    return () => {
      isMounted = false;
    };
  }, [multicallContract, gfuManagerContract, updateByTimer]);

  return { ids, loading };
};

export const useAllProjectsInfo = () => {
  const [projectsInfo, setProjectsInfo] = useState(null);
  const [loading, setLoading] = useState(true);
  const multicallContract = useMulticallContract();
  const gfuManagerContract = useGoFundUsManagerContract();
  const { account, chainInfo } = useWallet();
  const { ids } = useAllProjectIds();
  const projectCalls = useMemo(() => {
    if (!ids || ids.length === 0 || !gfuManagerContract.address) {
      return [];
    }
    return ids.map((id) => [
      gfuManagerContract.address,
      'projectRecords',
      [utils.formatBytes32String(id)],
    ]);
  }, [gfuManagerContract, ids]);

  const { loading: numberOfDonorsLoading, numberOfDonors } = useNumberOfDonorsByHandles(
    ids || null,
  );

  useEffect(() => {
    let isMounted = true;
    async function getProjects() {
      try {
        const results = await multicallHelper(
          multicallContract,
          GO_FUND_US_MANAGER_ABI.abi,
          projectCalls,
        );
        if (!results || !ids || !numberOfDonors) {
          return;
        }
        // console.log(results);
        if (isMounted) {
          setProjectsInfo(
            results
              .map((item, index) => ({
                ...decodeProjectInfo(item[0], account, chainInfo.shortName.toUpperCase()),
                numberOfDonors: numberOfDonors[index],
              }))
              .sort(
                (a, b) =>
                  parseFloat(b.totalDonation) * TOKEN_PRICE[b.donationTokenName.toUpperCase()] -
                  parseFloat(a.totalDonation) * TOKEN_PRICE[a.donationTokenName.toUpperCase()],
              ),
          );
        }
      } catch (e) {
        console.log(e);
      }
    }

    getProjects()
      .then(() => {
        if (isMounted) {
          setLoading(false);
        }
      })
      .catch((e) => {
        console.log(e);
      });
    return () => {
      isMounted = false;
    };
  }, [multicallContract, account, projectCalls, numberOfDonorsLoading, numberOfDonors, ids]);

  return {
    loading,
    projectsInfo,
  };
};

