export const useAccountProjectInvitationInfo = (id, optionalAccount = null) => {
  const { account } = useWallet();
  const gfuManagerContract = useGoFundUsManagerContract();
  const [loading, setLoading] = useState(true);
  const [totalInviteesAmount, setTotalInviteesAmount] = useState(0);
  const { inviteeLength } = useInviteeLength(id, optionalAccount);

  useEffect(() => {
    let isMounted = true;
    async function getInviteeLength() {
      if (account || optionalAccount) {
        const data = await gfuManagerContract.donationRecords(
          utils.formatBytes32String(id),
          optionalAccount || account,
        );
        if (data && isMounted) {
          setTotalInviteesAmount(utils.formatEther(data.totalInviteesAmount));
        }
      }
    }
    getInviteeLength()
      .then(() => {
        if (isMounted) {
          setLoading(false);
        }
      })
      .catch((e) => {
        console.log(e);
      });
    return () => {
      isMounted = false;
    };
  }, [id, account, optionalAccount, gfuManagerContract]);

  return {
    loading,
    inviteeLength,
    totalInviteesAmount,
  };
};

export const useProjectListInvitationInfo = (id) => {
  const [loading, setLoading] = useState(true);
  const [totalRefInfo, setTotalRefInfo] = useState(0);

  const accounts = useMemo(() => REFERRAL_LIST.map((item) => item.address), []);
  const { loading: lengthLoading, inviteeLength } = useListInviteeLength(id, accounts);
  const { loading: amountLoading, inviteeAmount } = useListInviteeAmount(id, accounts);
  useEffect(() => {
    let isMounted = true;
    async function getInviteeLength() {
      if (!lengthLoading && !amountLoading && inviteeLength && inviteeAmount && isMounted) {
        setTotalRefInfo(
          inviteeLength
            .map((len, index) => ({
              account: accounts[index],
              invitees: len,
              amounts: inviteeAmount[index],
            }))
            .filter((item) => item.invitees > 0),
        );
      }
    }
    getInviteeLength()
      .then(() => {
        if (isMounted) {
          setLoading(false);
        }
      })
      .catch((e) => {
        console.log(e);
      });
    return () => {
      isMounted = false;
    };
  }, [lengthLoading, amountLoading, inviteeLength, inviteeAmount]);

  return {
    loading,
    totalRefInfo,
  };
};
