import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Form, Input, message, Select, Slider } from 'antd';

import { useTranslation } from 'react-i18next';
import MainButton from '../../MainButton/MainButton';
import AvatarSelector from '../AvatarSelector/AvatarSelector';
import { useAuth } from '../../../hooks/useAuth';
import { useQuery } from '../../../hooks/useQuery';
import SendEmailVerify from '../SendEmailVerify/SendEmailVerify';
import {
  AUTH_MODAL_STATE,
  EMAIL_TYPE,
  ROLE_TYPE,
  ROLE_TYPE_INTERNAL,
} from '../../../utils/authUtils';

import styles from './Signup.module.scss';
import formStyles from '../AuthForm.module.scss';

const Signup = (props) => {
  const { toggleModal, switchContext } = props;
  const [form] = Form.useForm();
  const [avatar, setAvatar] = useState(0);
  const [avatarCollapsed, setAvatarCollapsed] = useState(true);
  const auth = useAuth();
  const [loading, setLoading] = useState(false);
  const query = useQuery()?.get('invitation');
  const role = useQuery()?.get('role');
  const { t } = useTranslation();

  const initialValues = {
    role: ROLE_TYPE.VOLUNTEER,
    donateRatio: 100,
  };

  const handleAvatarChange = (e) => {
    setAvatar(e);
  };

  const toggleAvatarCollapsed = () => {
    setAvatarCollapsed((prev) => !prev);
  };

  const handleRes = (res) => {
    const code = res?.code;
    let msg = '';
    switch (code) {
      case 0:
        msg = t('sign-up-success');
        message.success(msg);
        switchContext(AUTH_MODAL_STATE.LOGIN);
        return;
      case 1:
        msg = t('sign-up-email-already-registered');
        message.error(msg);
        form.setFields([{ name: 'email', errors: [msg] }]);
        return;
      case 2:
        msg = t('sign-up-invalid-code');
        message.error(msg);
        form.setFields([{ name: 'invitation', errors: [msg] }]);
        return;
      case 3:
        msg = t('sign-up-invalid-verification');
        message.error(msg);
        form.setFields([{ name: 'verification', errors: [msg] }]);
        return;
      default:
        msg = t('login-unknown-error');
        message.error(msg);
        console.log(res);
    }
  };

  const handleSignup = async () => {
    if (avatar === 0) {
      message.info(t('sign-up-select-avatar'));
      return;
    }
    let res;
    try {
      setLoading(true);
      await form.validateFields();
      const data = form.getFieldsValue(true);
      const payload = {
        nickname: data.nickname,
        email: data.email,
        icon: avatar,
        passwd: data.password,
        verify_code: data.verification,
        invitation_code: data.invitation,
        type: ROLE_TYPE_INTERNAL[data.role],
        donation: data.donateRatio,
      };
      res = await auth.signup(payload);
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
    if (res) {
      handleRes(res);
    }
  };

  useEffect(() => {
    if (query) {
      form.setFieldsValue({ invitation: query });
    }
    if (role && Object.values(ROLE_TYPE).includes(role)) {
      form.setFieldsValue({ role });
    }
  }, [query, form, role]);

  return (
    <div className={classNames(formStyles.AuthForm, styles.Signup)}>
      <div className={formStyles.titleWrap}>
        <h1>{t('auth-menu-signup-title')}</h1>
        <img src="/static/images/close.png" alt="close" onClick={toggleModal} />
      </div>
      <div className={styles.avatarWrap}>
        <div className={styles.avatarHeader}>
          <img
            className={styles.avatar}
            src={`/static/images/profile/avatar/${avatar}.png`}
            alt="avatar"
            onClick={toggleAvatarCollapsed}
          />
          <img
            className={styles.arrow}
            src={`/static/images/icons/${avatarCollapsed ? 'downArrow' : 'upArrow'}.png`}
            alt="arrow"
            onClick={toggleAvatarCollapsed}
          />
          <span className={styles.formLabel} onClick={toggleAvatarCollapsed}>
            {t('signup-select-avatar')}
          </span>
        </div>
        <div className={classNames(styles.collapseWrap, { [styles.collapsed]: avatarCollapsed })}>
          <AvatarSelector
            className={styles.avatarSelector}
            avatar={avatar}
            onClick={handleAvatarChange}
          />
        </div>
      </div>
      <Form layout="vertical" form={form} requiredMark="optional" initialValues={initialValues}>
        <Form.Item
          label={t('sign-up-nickname')}
          name="nickname"
          rules={[
            { required: true, message: t('referral-info-referral-nickname-alert') },
            {
              type: 'string',
              min: 3,
              max: 25,
              message: t('sign-up-length-warning'),
            },
            {
              pattern: /^[a-zA-Z0-9][a-zA-Z0-9_]+[a-zA-Z0-9]$/,
              message: t('sign-up-nickname-pattern'),
            },
          ]}>
          <Input placeholder={t('sign-up-enter-your-nickname')} />
        </Form.Item>
        <Form.Item
          label={t('sign-up-email')}
          name="email"
          rules={[
            { required: true, message: t('sign-up-enter-your-email') },
            { type: 'email', message: t('sign-up-enter-valid-email') },
          ]}>
          <Input placeholder={t('sign-up-enter-your-email-placeholder')} />
        </Form.Item>
        <Form.Item
          label={t('sign-up-password')}
          name="password"
          rules={[
            { required: true, message: t('sign-up-enter-your-password') },
            {
              type: 'string',
              min: 8,
              max: 25,
              message: t('reset-password-length-warning'),
            },
            {
              pattern: /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).+$/,
              message: t('reset-password-pattern-warning'),
            },
          ]}>
          <Input placeholder={t('sign-up-enter-your-password-placeholder')} type="password" />
        </Form.Item>
        <Form.Item
          name="confirm"
          label={t('sign-up-confirm-password')}
          dependencies={['password']}
          rules={[
            {
              required: true,
              message: t('reset-password-please-confirm-your-new-password'),
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error(t('sign-up-password-not-match')));
              },
            }),
          ]}>
          <Input placeholder={t('sign-up-signup-confirm-your-password')} type="password" />
        </Form.Item>

        <Form.Item
          label="Your Role"
          name="role"
          rules={[
            {
              required: true,
              message: 'A role is required',
            },
          ]}
          tooltip="Advocates can choose whether to donate their earning.">
          <Select>
            {Object.values(ROLE_TYPE).map((item) => (
              <Select.Option key={item} value={item}>
                {item.toUpperCase()}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
        <Form.Item dependencies={['role']} noStyle>
          {({ getFieldValue }) =>
            getFieldValue('role') === ROLE_TYPE.ADVOCATE ? (
              <Form.Item label="Donate Ratio" name="donateRatio" rules={[{ required: true }]}>
                <Slider
                  marks={{
                    0: 0,
                    50: '50%',
                    100: '100%',
                  }}
                />
              </Form.Item>
            ) : null
          }
        </Form.Item>

        <Form.Item label={t('sign-up-verification-code')} required>
          <Form.Item
            name="verification"
            rules={[{ required: true, message: t('sign-up-please-enter-your-verification-code') }]}
            noStyle>
            <Input style={{ width: '50%' }} placeholder={t('sign-up-enter-the-code')} />
          </Form.Item>

          <SendEmailVerify
            className={classNames(formStyles.link, formStyles.sendVerify)}
            form={form}
            type={EMAIL_TYPE.EMAIL_VERIFY}
          />
        </Form.Item>

        <Form.Item label={t('sign-up-invitation-code')} name="invitation">
          <Input placeholder={t('sign-up-enter-invitation-code')} />
        </Form.Item>
      </Form>

      <div className={formStyles.actionWrap}>
        <MainButton className={formStyles.actionButton} onClick={handleSignup} loading={loading}>
          {t('deseign-submit')}
        </MainButton>
        <p>
          {t('sign-up-have-an-account')}{' '}
          <span
            className={formStyles.link}
            onClick={() => {
              switchContext(AUTH_MODAL_STATE.LOGIN);
            }}>
            {t('auth-menu-login')}
          </span>
        </p>
      </div>
    </div>
  );
};

Signup.propTypes = {
  toggleModal: PropTypes.func.isRequired,
  switchContext: PropTypes.func.isRequired,
};

Signup.defaultProps = {};

export default Signup;
