import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import numeral from 'numeral';

import { useTranslation } from 'react-i18next';
import { useAuth } from '../../../hooks/useAuth';

import styles from './ProfileOverview.module.scss';
import MainButton from '../../MainButton/MainButton';

const ProfileOverview = (props) => {
  const { toggleModal } = props;
  const { info, logout } = useAuth();
  const { t } = useTranslation();
  return (
    <div className={styles.ProfileOverview}>
      {info && (
        <>
          <div className={styles.titleWrap}>
            <h1>{t('profile-title')}</h1>
            <img src="/static/images/close.png" alt="close" onClick={toggleModal} />
          </div>
          <div className={styles.contentWrap}>
            <div className={styles.header}>
              <img src="/static/images/auth/profileOverview/headerBg.png" alt="header" />
            </div>
            <div className={styles.infoWrap}>
              <img
                className={styles.avatar}
                src={`/static/images/profile/avatar/${info.icon}.png`}
                alt="avatar"
              />
              <h2 className={styles.nickname}>{info.nickname}</h2>
              <div className={styles.itemValue}>
                <img src="/static/images/icons/pointsicon.png" alt="points" />
                <span className={styles.fontFix}>
                  {numeral(Number(info.point_total) / 1000000).format('0,0.[00]')}
                </span>
              </div>
              <Link onClick={toggleModal} className={styles.profileLink} to="/profile">
                {t('profile-overview-detail')}
                <img src="/static/images/icons/rightBracket.svg" alt="bracket" />
              </Link>
            </div>
          </div>
          <MainButton
            className={styles.logoutButton}
            secondary
            onClick={() => {
              toggleModal();
              logout();
            }}>
            {t('account-info-logout')}
          </MainButton>
        </>
      )}
    </div>
  );
};

ProfileOverview.propTypes = { toggleModal: PropTypes.func.isRequired };

export default ProfileOverview;
