import React, { useEffect, useState } from 'react';
import { Modal } from 'antd';

import { AUTH_MODAL_STATE } from '../../../utils/authUtils';
import Login from '../Login/Login';
import Signup from '../Signup/Signup';
import { useAuthMenu } from '../../../hooks/useAuthMenu';

import styles from './AuthMenu.module.scss';
import ProfileOverview from '../ProfileOverview/ProfileOverview';
import ResetPassword from '../ResetPassword/ResetPassword';

const AuthMenu = () => {
  const [modalState, setModalState] = useState(AUTH_MODAL_STATE.LOGIN);
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [showProfileModal, setShowProfileModal] = useState(false);
  const {
    setToggleShowAuthModal,
    setShowSignupModal,
    setShowLoginModal,
    setToggleShowProfileModal,
  } = useAuthMenu();

  const toggleShowProfileModal = () => {
    setShowProfileModal((prev) => !prev);
  };

  const toggleShowAuthModal = () => {
    setShowAuthModal((prev) => !prev);
  };

  const showSignupModal = () => {
    setModalState(AUTH_MODAL_STATE.SIGNUP);
    setShowAuthModal(true);
  };

  const showLoginModal = () => {
    setModalState(AUTH_MODAL_STATE.LOGIN);
    setShowAuthModal(true);
  };

  useEffect(() => {
    setToggleShowAuthModal(() => toggleShowAuthModal);
    setShowSignupModal(() => showSignupModal);
    setShowLoginModal(() => showLoginModal);
    setToggleShowProfileModal(() => toggleShowProfileModal);
  }, [setToggleShowAuthModal, setShowSignupModal, setShowLoginModal, setToggleShowProfileModal]);

  const getModalContent = () => {
    switch (modalState) {
      case AUTH_MODAL_STATE.LOGIN:
        return (
          <Login
            toggleModal={toggleShowAuthModal}
            switchContext={(context) => {
              setModalState(context);
            }}
          />
        );
      case AUTH_MODAL_STATE.SIGNUP:
        return (
          <Signup
            toggleModal={toggleShowAuthModal}
            switchContext={(context) => {
              setModalState(context);
            }}
          />
        );
      case AUTH_MODAL_STATE.RESET:
        return (
          <ResetPassword
            toggleModal={toggleShowAuthModal}
            switchContext={(context) => {
              setModalState(context);
            }}
          />
        );
      default:
        return null;
    }
  };

  return (
    <div className={styles.AuthMenu}>
      <Modal
        visible={showAuthModal}
        onCancel={toggleShowAuthModal}
        wrapClassName={styles.walletModalWrapper}
        width={350}
        centered
        closable={false}
        footer={null}
        destroyOnClose>
        {getModalContent()}
        {/* {modalState === AUTH_MODAL_STATE.LOGIN && (
          <Login
            toggleModal={toggleShowAuthModal}
            switchContext={() => {
              setModalState(AUTH_MODAL_STATE.SIGNUP);
            }}
          />
        )}
        {modalState === AUTH_MODAL_STATE.SIGNUP && (
          <Signup
            toggleModal={toggleShowAuthModal}
            switchContext={() => {
              setModalState(AUTH_MODAL_STATE.LOGIN);
            }}
          />
        )} */}
      </Modal>
      <Modal
        visible={showProfileModal}
        onCancel={toggleShowProfileModal}
        wrapClassName={styles.walletModalWrapper}
        width={350}
        centered
        closable={false}
        footer={null}>
        <ProfileOverview toggleModal={toggleShowProfileModal} />
      </Modal>
    </div>
  );
};

export default AuthMenu;
