// add leading zeros

export const padLeadingZeros = (num, size) => {
  let s = `${num}`;
  while (s.length < size) s = `0${s}`;
  return s;
};

export const FETCH_STATE = {
  INIT: 0,
  SUCCESS: 1,
  FAILURE: 2,
};

// reducer for general contract calls including states: loading, error, data
export const readContractReducer = (state, action) => {
  switch (action.type) {
    case FETCH_STATE.INIT:
      return {
        ...state,
        isLoading: true,
        isError: false,
      };
    case FETCH_STATE.SUCCESS:
      return {
        ...state,
        isLoading: false,
        isError: false,
        data: action.payload,
      };
    case FETCH_STATE.FAILURE:
      return {
        ...state,
        isLoading: false,
        isError: true,
      };
    default:
      throw new Error('Invalid general contract action');
  }
};

export const padInvitationCode = (code) => `0x${code.padEnd(64, '0')}`;
