import React, { useEffect } from 'react';
import { useAuth } from '../../../hooks/useAuth';
import { useAuthMenu } from '../../../hooks/useAuthMenu';
import { useQuery } from '../../../hooks/useQuery';

const Invitation = () => {
  const query = useQuery()?.get('invitation');
  const role = useQuery()?.get('role');
  const { info } = useAuth();
  const authMenu = useAuthMenu();

  useEffect(() => {
    const timer = setTimeout(() => {
      if ((query || role) && !info && authMenu) {
        authMenu.showSignupModal();
      }
    }, 1000);
    return () => {
      clearTimeout(timer);
    };
  }, [authMenu, info, query, role]);
  return <div />;
};

export default Invitation;
