import React, { useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Modal, message } from 'antd';
import { useTranslation } from 'react-i18next';
import { useAuth } from '../../../hooks/useAuth';
import BindModal from './BindModal/BindModal';
import { useWallet } from '../../../hooks/useWallet';
import ResetPassword from '../ResetPassword/ResetPassword';

import styles from './AccountInfo.module.scss';

const AccountInfo = (props) => {
  const { className } = props;
  const { info, logout } = useAuth();
  const [showModal, setShowModal] = useState(false);
  const { t } = useTranslation();
  const [showBindModal, setShowBindModal] = useState('');
  const { account } = useWallet();

  const logoutHelper = () => {
    logout();
  };

  const toggleShowModal = () => {
    setShowModal((prev) => !prev);
  };

  const toggleBindModal = () => {
    setShowBindModal((prev) => !prev);
  };

  const handleBindAddress = () => {
    if (account) {
      toggleBindModal();
    } else {
      message.info(t('project-action-please-connect-your-wallet'));
    }
  };
  return (
    <div className={classNames(styles.AccountInfoWrapper, className)}>
      <div className={styles.accountHeader}>
        <h3>{t('account-info-account')}</h3>
        <div className={styles.logOutHelper} onClick={logoutHelper}>
          <img src="/static/images/profile/accountInfo/logoutIcon.png" alt="logout" />
          <span>{t('account-info-logout')}</span>
        </div>
      </div>
      <div className={styles.accountDetail}>
        <div className={styles.account}>
          <img src={`/static/images/profile/avatar/${info?.icon}.png`} alt="headicon" />
          <p>{info?.nickname}</p>
          <p>{info?.email}</p>
          <div className={styles.addressWrap}>
            {info?.wallet && <div className={styles.address}>{info.wallet}</div>}
            {!info?.wallet && (
              <div className={classNames(styles.link)} onClick={handleBindAddress}>
                {t('bind-modal-bind-address')}
              </div>
            )}
          </div>
          <div className={styles.link} onClick={toggleShowModal}>
            {t('account-info-reset-password')}
          </div>
        </div>
      </div>
      <Modal
        title={null}
        visible={showModal}
        onCancel={toggleShowModal}
        wrapClassName={styles.walletModalWrapper}
        width={350}
        centered
        closable={false}
        destroyOnClose
        footer={null}>
        <ResetPassword toggleResetPasswordModal={toggleShowModal} />
      </Modal>
      <Modal
        title={null}
        visible={showBindModal}
        onCancel={toggleBindModal}
        wrapClassName={styles.walletModalWrapper}
        width={350}
        closable={false}
        centered
        destroyOnClose
        footer={null}>
        <BindModal toggleBindModal={toggleBindModal} />
      </Modal>
    </div>
  );
};

AccountInfo.propTypes = {
  className: PropTypes.string,
};

AccountInfo.defaultProps = {
  className: '',
};
export default AccountInfo;
