import React, { Suspense } from 'react';
import ReactDOM from 'react-dom';
import { HashRouter } from 'react-router-dom';
import App from './App';
import { ProvideWallet } from './hooks/useWallet';
import './index.scss';

import './i18n';
import { ProvideUpdate } from './hooks/useUpdate';
import Loading from './components/Loading/Loading';
import { ProvideQuery } from './hooks/useQuery';
import { ProvideAuth } from './hooks/useAuth';
import { ProvideAuthMenu } from './hooks/useAuthMenu';

ReactDOM.render(
  <React.StrictMode>
    <HashRouter>
      <Suspense fallback={<Loading />}>
        <ProvideQuery>
          <ProvideWallet>
            <ProvideUpdate>
              <ProvideAuth>
                <ProvideAuthMenu>
                  <App />
                </ProvideAuthMenu>
              </ProvideAuth>
            </ProvideUpdate>
          </ProvideWallet>
        </ProvideQuery>
      </Suspense>
    </HashRouter>
  </React.StrictMode>,
  document.getElementById('root'),
);
// eslint-disable-next-line
const consoleError = console.error.bind(console);
// eslint-disable-next-line
console.error = (errObj, ...args) => {
  if (
    process.env.NODE_ENV === 'development' &&
    (typeof errObj.message === 'string' || typeof errObj === 'string') &&
    args.includes('findDOMNode')
  ) {
    return;
  }
  consoleError(errObj, ...args);
};
