import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Form, Input, message } from 'antd';
import classNames from 'classnames';
import { useTranslation } from 'react-i18next';
import { useAuth } from '../../../hooks/useAuth';
import MainButton from '../../MainButton/MainButton';

import styles from './ResetPassword.module.scss';
import formStyles from '../../Auth/AuthForm.module.scss';

const ResetPassword = (props) => {
  const { toggleResetPasswordModal } = props;
  const { changePassword, logout } = useAuth();
  const [loading, setLoading] = useState(false);
  const [form] = Form.useForm();
  const { t } = useTranslation();

  const handleRes = (res) => {
    const code = res?.code;
    let msg = '';
    switch (code) {
      case 0:
        msg = t('reset-password-success');
        message.success(msg);
        toggleResetPasswordModal();
        setTimeout(() => {
          logout();
        }, 1000);
        return;
      case 1:
        msg = t('reset-password-incorrect-password');
        message.error(msg);
        form.setFields([{ name: 'oldPassword', errors: [msg] }]);
        return;
      default:
        msg = t('reset-password-unknown-error');
        message.error(msg);
        console.log(res);
    }
  };

  const submitHandler = async () => {
    try {
      setLoading(true);
      await form.validateFields();
      const data = form.getFieldsValue(true);
      const payload = { old_passwd: data.oldPassword, new_passwd: data.newPassword };
      const res = await changePassword(payload);
      handleRes(res);
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className={classNames(styles.resetPasswordWrapper, formStyles.AuthForm)}>
      <div className={formStyles.titleWrap}>
        <h1>{t('reset-password-reset-your-password')}</h1>
        <img src="/static/images/close.png" alt="close" onClick={toggleResetPasswordModal} />
      </div>
      <Form layout="vertical" form={form} requiredMark={false}>
        <Form.Item
          label={t('reset-old-password')}
          name="oldPassword"
          rules={[{ required: true, message: t('reset-password-please-enter-your-old-password') }]}>
          <Input placeholder={t('reset-old-password')} type="password" />
        </Form.Item>
        <Form.Item
          label={t('reset-new-password')}
          name="newPassword"
          rules={[
            { required: true, message: t('reset-password-please-enter-your-new-password') },
            {
              type: 'string',
              min: 8,
              max: 25,
              message: t('reset-password-length-warning'),
            },
            {
              pattern: /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).+$/,
              message: t('reset-password-pattern-warning'),
            },
          ]}>
          <Input type="password" placeholder={t('reset-new-password')} />
        </Form.Item>
        <Form.Item
          name="confirmPassword"
          label={t('reset-confirm-password')}
          dependencies={['New Password']}
          rules={[
            {
              required: true,
              message: t('reset-password-please-confirm-your-new-password'),
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('newPassword') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error(t('sign-up-password-not-match')));
              },
            }),
          ]}>
          <Input type="password" placeholder={t('reset-confirm-password')} />
        </Form.Item>
      </Form>
      <div className={styles.actionWrap}>
        <MainButton
          secondary
          className={styles.actionButton}
          loading={loading}
          onClick={submitHandler}>
          {t('deseign-submit')}
        </MainButton>
      </div>
    </div>
  );
};

ResetPassword.propTypes = {
  toggleResetPasswordModal: PropTypes.func.isRequired,
};

export default ResetPassword;
