import React, { useEffect } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import MainContainer from '../../components/UI/MainContainer/MainContainer';
import AccountInfo from '../../components/Profile/AccountInfo/AccountInfo';
import ReferralInfo from '../../components/Profile/ReferralInfo/ReferralInfo';
import { useAuth } from '../../hooks/useAuth';

import styles from './Profile.module.scss';
import Loading from '../../components/Loading/Loading';

const Profile = () => {
  const { info, getInfo } = useAuth();
  const history = useHistory();
  const { search } = useLocation();
  const { t } = useTranslation();

  useEffect(() => {
    const timer = setTimeout(() => {
      if (!info) {
        history.push({ pathname: '/', search: `?${new URLSearchParams(search).toString()}` });
        // message.info(t('profile-login-warning'));
      }
    }, 1000);
    return () => {
      clearTimeout(timer);
    };
  }, [info, history, search, t]);

  useEffect(() => {
    getInfo();
  }, [getInfo]);

  return (
    <div className={styles.NewProfileWrapper}>
      {info && (
        <>
          <section
            className={styles.banner}
            style={{ backgroundImage: 'url(/static/images/profile/bannerBg.png)' }}>
            <div className={styles.innerWrapper}>
              {/* <MainContainer className={styles.bannerContainer}>
                <h2>{t('profile-profile')}</h2>
              </MainContainer> */}
            </div>
          </section>
          <MainContainer className={styles.mainContent}>
            <AccountInfo />
            <ReferralInfo />
          </MainContainer>
        </>
      )}
      {!info && <Loading />}
    </div>
  );
};

export default Profile;
