import React, { useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { useTranslation } from 'react-i18next';
import { message } from 'antd';
import { useAuth } from '../../../../hooks/useAuth';
import { useWallet } from '../../../../hooks/useWallet';
import MainButton from '../../../MainButton/MainButton';

import formStyles from '../../../Auth/AuthForm.module.scss';
import styles from './BindModal.module.scss';

const BindModal = ({ toggleBindModal }) => {
  const { info, bindAddress, getInfo } = useAuth();
  const [loading, setLoading] = useState(false);
  const { t } = useTranslation();
  const wallet = useWallet();

  const handleRes = (res) => {
    const code = res?.code;
    let msg = '';
    switch (code) {
      case 0:
        msg = t('bind-modal-bind-successfully');
        message.success(msg);
        getInfo();
        toggleBindModal();
        return;
      case 1:
        msg = t('funding-configuration-error-invalid-address');
        message.error(msg);
        return;
      case 2:
        msg = t('bind-modal-account-already-bound');
        message.error(msg);
        return;
      case 4:
        msg = t('bind-modal-address-already-bound');
        message.error(msg);
        return;
      default:
        msg = t('reset-password-unknown-error');
        message.error(msg);
        console.log(res);
    }
  };
  const bindHandler = async () => {
    if (info.wallet) {
      message.error(t('bind-modal-account-already-bound'));
      return;
    }

    if (!wallet.account) {
      message.error(t('design-form-message-please-connect-to-wallet'));
      return;
    }

    try {
      setLoading(true);
      const res = await bindAddress(wallet.account);
      handleRes(res);
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className={classNames(styles.BindModal, formStyles.AuthForm)}>
      <div className={formStyles.titleWrap}>
        <h1>{t('bind-modal-bind-address')}</h1>
        <img src="/static/images/close.png" alt="close" onClick={toggleBindModal} />
      </div>

      <p className={styles.notes}>{t('bind-modal-confirm-sentence')}</p>
      <div className={styles.walletAddressWrapper}>
        <div className={styles.address}>{wallet.account}</div>
      </div>
      <MainButton className={styles.bindButton} onClick={bindHandler} loading={loading}>
        {t('bind-modal-bind')}
      </MainButton>
    </div>
  );
};

BindModal.propTypes = {
  toggleBindModal: PropTypes.func.isRequired,
};

export default BindModal;
