import { useState, useContext, createContext } from 'react';
import PropTypes from 'prop-types';

const authMenuContext = createContext();

export const useAuthMenu = () => useContext(authMenuContext);

const useProvideAuthMenu = () => {
  const [toggleShowAuthModal, setToggleShowAuthModal] = useState(null);
  const [showSignupModal, setShowSignupModal] = useState(null);
  const [showLoginModal, setShowLoginModal] = useState(null);
  const [toggleShowProfileModal, setToggleShowProfileModal] = useState(null);
  const [toggleShowWalletListModal, setToggleShowWalletListModal] = useState(null);

  return {
    toggleShowAuthModal,
    setToggleShowAuthModal,
    showSignupModal,
    setShowSignupModal,
    showLoginModal,
    setShowLoginModal,
    toggleShowProfileModal,
    setToggleShowProfileModal,
    toggleShowWalletListModal,
    setToggleShowWalletListModal,
  };
};

export const ProvideAuthMenu = ({ children }) => {
  const authMenu = useProvideAuthMenu();
  return <authMenuContext.Provider value={authMenu}>{children}</authMenuContext.Provider>;
};

ProvideAuthMenu.propTypes = {
  children: PropTypes.node.isRequired,
};
